@use "../../util/" as *;

.divider-top {
    @include divider(top);
    border: 1px solid red;
}

main {
    position: relative;
    // Work on setting up a grid layout for the game schedule.

    .divider-bottom {
        @include divider(bottom, 180);
    }

    .divider-icon {
        @include divider-icon;
    }
    .hero-section {
        position: relative;
        padding: var(--fs1) var(--fs6);

        .hero-content {
            .hero-images-collage {
                display: flex;
                justify-content: center;
                margin-bottom: var(--fs1);
            }

            .hero-img-1 {
                width: 50%;
                transform: translate(20%, 0);
                z-index: -1;
            }

            .hero-img-2 {
                width: 50%;
                transform: translate(-15%, 25%);
                z-index: 0;
            }

            .hero-text-container {
                & h2 {
                    margin-bottom: var(--fs6);
                }

                & p {
                    margin-bottom: var(--fs9);
                }

                & p:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}
